import React from 'react'
import styled from 'styled-components'
import { Link } from 'react-router-dom'

const Div = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 20vw;
  margin: 0;
  padding: 0;
`

const ContainerDiv = Div.extend`
  height: 100vh;
  background-color: #efefef;
`

const NameDiv = Div.extend`
  height: 6rem;
  background-color: #046d18;
  color: #161717;
`

// const MenuItemDiv = Div.extend`
//   width: 50%;
//   margin: 0;
//   padding: 0;
//   background-color: yellow;

// `

const P = styled.p`
  margin: 0;
  padding: 0;
  color: #fcfafa;
`

const Ul = styled.ul`
  // display: flex;
  // flex-direction: column;
  // justify-content: space-around;
  // align-items: center;
  margin: 0;
  padding: 0;
  padding-top: 0.8rem;
  list-style: none;
  height: 90%;
  


  @media (min-width: 742px) {
    font-size: 2.2rem;
    // This width is pivotal for the sidebar menu
    width: 30rem;
  }
`

const Li = styled.li`
  color: #828489;
  display: flex;
  align-items: center;
  float: left;
  margin-top: 2.8rem;
  margin-bottom: 1rem;
  margin-left: 8.4rem;
  // margin-right: 8rem;
  color: #161717;
  font-size: 1.4rem;

  &:hover {
    color: #d0ccd0;
  }
`


const navbar = ({ location, menuItems }) => {
  const activeLink = {
    'color': '#046d18',
    'fill': '#046d18'
  }
  console.log('in navbar')
  console.log(location)

  return(
    <ContainerDiv>
      <NameDiv>
        <P>Welcome,</P>
        <P>James Good</P>
      </NameDiv>
      <Ul>      
        { menuItems.map(menuItem => {
            return <Link key={ menuItem.name } style={{ 'textDecoration': 'none' }} to={ menuItem.path }>
                    <Li style={ (menuItem.path === location.pathname) ? activeLink : null }>
                      { <menuItem.icon/> }{ menuItem.name }
                    </Li>
                   </Link>
          } 
        )}
      </Ul>
    </ContainerDiv>
  )
}

export default navbar


